*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: "Bermina Sans";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
}

@font-face {
  font-family: 'GothamHTF-Book';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/GothamHTF-Book.woff") format("woff");
}

* {
  padding: 0;
  margin: 0;
  font-family: "Bermina Sans";
  color: #05445a;
}

.small {
  font-size: 0.5em;
}

.inner {
  width: 100%;
  margin: 0 auto;
  padding: 5px;
}

header {
  background-image: url("../images/background.jpg?1");
  background-position: top center;
  background-size: cover;
  height: 500px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

header .promo {
  position: absolute;
  border-radius: 5px;
  background-color: firebrick;
  transform: rotate(25deg);
  right: 50px;
  top: 50px;
  color: white;
}

header .promo p {
  padding: 5px 10px;
  margin-bottom: 0;
  font-size: 1.2em;
  line-height: 22px;
  color: white;
}

header .promo p small {
  color: white;
  font-size: 0.5em;
}

header .bandeau {
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.5);
  width: 100%;
  margin-bottom: 15px;
}

header .bandeau p {
  padding-left: 15px;
  padding-top: 0;
  margin-bottom: 0;
  color: white;
}

header .bandeau p small {
  color: white;
  font-size: 0.65em;
}

.logo-button {
  margin: 10px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.logo-button button {
  padding: 0 2px;
  border-width: 1px;
  background-color: white;
  border-radius: 5px;
  border-color: black;
  font-size: 1.5em;
}

.logo-button button:hover {
  background-color: #05445a;
  color: white;
}

.contenu {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.contenu h1 {
  font-size: 2.7em;
  margin-bottom: 40px;
}

.titre {
  display: flex;
  justify-content: flex-start;
}

.titre p {
  font-family: "GothamHTF-Book";
  letter-spacing: 5px;
}

.liste-img {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.liste-img > div {
  padding: 0 5px;
}

.liste-img > div img {
  width: 100%;
}

.liste-img > div p {
  text-align: center;
  padding-top: 10px;
  font-family: "GothamHTF-Book";
}

@media screen and (min-width: 480px) {
  .move {
    order: -1;
  }
  .liste-img div {
    padding: 0 50px;
    width: 50%;
  }
}

@media screen and (min-width: 768px) {
  header {
    height: 81vh;
  }
  header .bandeau {
    display: flex;
    flex-direction: row;
  }
  header .bandeau p {
    font-size: 1.5em;
  }
  .inner {
    width: 768px;
  }
  .titre {
    justify-content: flex-end;
  }
  .liste-img > div {
    padding: 0 50px;
    width: 33.3%;
  }
}

.gym, .smartphone {
  border-radius: 50%;
}
